<template>
  <transition class="preductdetail">
    <div class="food" v-show="showFlag" ref="foodView">
            <div class="food-wrapper">
                <div class="food-content">
                    <div class="img-wrapper">
                        <img class="food-img" :src="food.picture">
                        <i class="close-bt bi bi-x-circle" @click="closeView"></i>
                        <img class="share-bt" src="../assets/share.png">
                        <img class="more-bt" src="../assets/more.png">
                    </div>
                    
                    <div class="content-wrapper">
                        <h3 class="name">{{food.name}}</h3>
                        <p class="saled">{{food.month_saled_content}}</p>
                        <img class="product" v-show="food.product_label_picture" :src="food.product_label_picture">

                        <p class="price">
                            <span class="text">¥{{food.min_price}}</span>
                            <span class="unit">/{{food.unit}}</span>
                        </p>

                        
                        <div class="buy"  @click="addSunt">
                            选规格
                        </div>
                    </div>
                </div>

				<!-- 选规格 -->
				<div class="buy-warpper" v-show="onFunt">
					<div class="buy-top">
						<div class="butts-item" :style="isAcive=='好吃' ? acStyle : ''" @click="clickHandler" >好吃</div>
						<div class="butts-item" :style="isAcive=='不好吃' ? acStyle : ''" @click="clickHandler" >不好吃</div>
					</div>
					<div class="buy-bottom">
						<div class="cartcontrol-wrapper">
                            <CartconTrol :food="food"></CartconTrol>
                        </div>
                        <div class="buy" v-show="!food.count || food.count == 0" @click="addProduct">
                            添加
                        </div>
					</div>
					<div class="close-wrapper" >
                  <!-- <span class="icon-close" @click="closeBulletin"></span> -->
						<i class="bi bi-x-lg" @click="closeFunt"></i>
					</div>
				</div>

				
				<SplitLng></SplitLng>
                

				<!-- 外卖评价 -->
				<div class="rating-wrapper">
					<!-- 评价头部 -->
					<div class="rating-title">
						<div class="like-ratio" v-if="food.rating">
							<span class="title">{{food.rating.title}}</span>
							<span class="ratio">
								(
									{{food.rating.like_ratio_desc}}
									<i>{{food.rating.like_ratio}}</i>
								)
							</span>
						</div>
						<div class="snd-title" v-if="food.rating">
							<span class="text">{{food.rating.snd_title}}</span>
							<i class="bi bi-chevron-right icon"></i>
						</div>
					</div>
					

					<ul class="rating-content" v-if="food.rating">
						<li v-for="(comment,index) in food.rating.comment_list" :key="index" class="comment-item">
							<div class="comment-header">
								<img :src="comment.user_icon" v-if="comment.user_icon" />
								<img src="../assets/anonymity.png" v-if="!comment.user_icon"  />
							</div>
							<div class="comment-main">
								<div class="user">
								{{comment.user_name}}
								</div>
								<div class="time">
								{{comment.comment_time}}
								</div>
								<div class="content">
								{{comment.comment_content}}
								</div>
							</div>
						</li>
					</ul>
				</div>
				
            </div>
        </div>
  </transition>
</template>

<script>
import Vue from 'vue'
import BScroll from 'better-scroll'
import SplitLng from './SplitLng'
import CartconTrol from './CartconTrol'

export default {
  name: "ProductDetail",
  data() {
    return {
		showFlag: false,
		onFunt: false,
		isAcive:'',
		acStyle: {
			background: '#FB4E44',
			color:'#fff'
		}
      }
    },
  props: {
    food: {
        type:Object
      }
  },
  methods: {
    showView() {
      this.showFlag = true

      this.$nextTick(() => {
        if (!this.scroll) {
          //实例化 滚动效果
          this.scroll = new BScroll(this.$refs.foodView, {
              click:true
            })
        } else {
            this.scroll.refresh()
          }
        })
    },
    closeView() {
        this.showFlag = false
    },
    addProduct() {
        Vue.set(this.food,"count",1)
	// eslint-disable-next-line no-mixed-spaces-and-tabs
	},
	addSunt() {
 // eslint-disable-next-line no-mixed-spaces-and-tabs
		  this.onFunt = true
	},
	closeFunt() {
	this.onFunt = false
// eslint-disable-next-line no-mixed-spaces-and-tabs
	},
	clickHandler(event) {
		this.isAcive = event.target.innerText
	}
	
    },
  components: {
    SplitLng,
    CartconTrol
  }
}
</script>

