<template>
  <div class="cartcontrol">
    <transition name="move">
            <div class="cart-decrease bi bi-dash-circle" @click.stop.prevent="decreaseCart" v-show="food.count"></div>            
      </transition>
      <div class="cart-count" v-show="food.count">{{food.count}}</div>
      <div class="cart-add bi bi-plus-circle-fill" @click.stop.prevent="increaseCart">
          <i class="bg"></i>
      </div>
  </div>
</template>

<script>
import Vue from 'vue'
export default {
  name: 'CartconTrol',
    props: {
      food: {
          type:Object
      }
  },
  methods: {
    //点击事件  加  减
        decreaseCart() {
            // console.log(111);
            // eslint-disable-next-line vue/no-mutating-props
            this.food.count--
        },
        increaseCart() {
            // console.log(222);
            if (!this.food.count) {
                    Vue.set(this.food,"count",1)
            } else {
                // eslint-disable-next-line vue/no-mutating-props
                this.food.count++
            }
        }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>


.cartcontrol{
	font-size: 0;
}
.cartcontrol .cart-decrease{
	display: inline-block;
	width: 26px;
	height: 26px;
	font-size: 26px;
	color: #b4b4b4;
}
.cartcontrol .cart-add .bg{
	width: 20px;
	height: 20px;
	border-radius: 50%;
	background: black;
	position: absolute;
	left: 3px;
	top: 3px;
	z-index: -1;
}
.cartcontrol .cart-count{
	display: inline-block;
	width: 25px;
	text-align: center;
	font-size: 12px;
	line-height: 26px;
	vertical-align: top;
}
.cartcontrol .cart-add{
	display: inline-block;
	width: 26px;
	height: 26px;
	font-size: 26px;
	color: #ffd161;
	position: relative;
}
.move-enter-active,.move-leave-active{
	transition: all 0.5s linear;
}
.move-enter,.move-leave-to{
	transform: translateX(20px) rotate(180deg);
}

</style>
